<template>

	<ul class="dropdown-menu">
		<li>
			<a href="javascript:void(0)" @click.prevent="$router.push('/index')">
				<i class="fa fa-file w20 text-center"></i>
				上传文件
			</a>
		</li>
		<li>
			<a href="javascript:void(0)" @click.prevent="$router.push('/index')">
				<i class="fa fa-folder w20 text-center"></i>
				上传文件夹
			</a>
		</li>
		<li class="divider"></li>
	</ul>


</template>

<script>

  export default {
    data () {
      return {
        user: this.$store.state.user
      }
    },
    props: {},
    computed: {},
    methods: {
      logout () {
        this.user.httpLogout()
        this.$router.push('/user/login')
      }
    },
    mounted () {
    }
  }
</script>
<style lang="less" rel="stylesheet/less">
	.dropdown-menu {
		top: 20px;
		left: auto;
		right: 0;
	}
</style>



