<template>
	<div class="top-navigation-bar">
		<nav class="navbar border0 mb0">

			<div class="navbar-title" :class="{'text-center':!$store.state.config.mobile}">
				<router-link to="/">
					<span class="f22 black bold">
						<span class="fa fa-github f30 text-primary"></span>
						Tank
					</span>
				</router-link>
			</div>


			<button class="drawer-trigger btn btn-primary btn-sm"
			        @click.stop.prevent="$store.state.config.showDrawer=!$store.state.config.showDrawer">
				<i class="fa fa-bars"></i>
			</button>


		</nav>
	</div>
</template>
<script>
  import AvatarNaviBarDropdown from './widget/AvatarNaviBarDropdown.vue'

	export default {
    data() {
      return{
        user: this.$store.state.user
      }
    },
		components:{
      AvatarNaviBarDropdown
		}
	}
</script>
<style lang="less" rel="stylesheet/less">

	@import "../../assets/css/global/variables";

	//上方的导航栏
	.top-navigation-bar {

		height: @top-navigation-height;
		line-height: @top-navigation-height;
		background-color: #fff;
		border-bottom: 1px solid #eeeeee;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 10;
	/*	padding-left: @sidebar-width;*/

		//手机屏幕
		@media (max-width: @screen-xs-max) {
			padding-left: 0;
		}

		.navbar-title {
			float: left;
			width: @top-logo-width;
			height: @top-navigation-height;
			padding: 0 10px;
			cursor: pointer;
			//background-color: #e3f3f4;
			/*@media (min-width: 768px) {
				margin-top: 5px;
				margin-left: 10px;
			}
			@media (max-width: 768px) {
				margin-top: 5px;
				margin-left: 10px;
			}*/

			.logo-img {
				height: 30px;
			}

			.logo-title {
				line-height: 30px;
				font-size: 13px;
				color: #778195;

				//手机屏幕
				@media (max-width: @screen-xs-max) {
					//display: none;
					font-size: 12px;
				}
			}

		}

		.navbar-menu{
			float: left;
			li{
				display: inline;
				padding-bottom: 8px;
				font-size: 17px;
				margin-right: 50px;
				color: #555;
				cursor: pointer;
				&.active {
					color: @brand-primary;
					border-bottom: 3px solid @brand-primary;
				}
			}
		}

		.login-trigger {
			float: right;
			margin-right: 10px;
			.login-span, .register-span {
				color: #888;
				cursor: pointer;
				font-size: 14px;
				line-height: 40px;

				&:hover {
					color: black;
					/*text-decoration: underline;*/
				}
			}
		}

		.drawer-trigger {
			float: right;
			margin-top: 15px;
			margin-right: 5px;
		}

		.navbar-right {

			> li {
				display: inline-block;
				&:last-child {
					margin-right: 5px;
				}
				a {
					color: #999c9e;
					font-size: 14px;
					padding: 6px 12px;
				}

			}

			@media (min-width: 768px) {
				float: right !important;
				margin-right: 0;
			}

			.message-info {
				position: relative;

				.label {

					line-height: 12px;
					padding: 2px 5px;
					position: absolute;
					left: 10%;
					bottom: 50%;

				}
			}
		}

		//小屏幕
		@media (min-width: @screen-sm-min) {
			.drawer-trigger {
				display: none;
			}
		}
		//手机屏幕
		@media (max-width: @screen-xs-max) {

			.navbar-right {
				display: none;
			}
		}


	}


</style>


