<template>
	<div class="animated fadeIn hp100">
		<LoginFrame>
			<div>

				<div class="text-primary f25 text-center mb20">欢迎登录</div>
				<!--输入框开始-->
				<div class="input-group mb15">
					<span class="input-group-addon"><i class="fa fa-user w14"></i></span>
					<input type="email" class="form-control" placeholder="邮箱" v-model="user.email"
					       @keyup.enter="login">
				</div>

				<div class="input-group mb15">
					<span class="input-group-addon"><i class="fa fa-unlock-alt w14"></i></span>
					<input type="password" class="form-control" placeholder="密码" v-model="user.localPassword"
					       @keyup.enter="login">
				</div>

				<!--按钮-->
				<div class="row mt10">
					<div class="col-md-12 mb10">
						<button class="btn btn-primary button full-width" @click.prevent.stop="login">登录</button>
						<!--<span class="text-default pull-right mt5 cursor" @click.prevent.stop="reset">重置密码</span>-->
					</div>
				</div>

			</div>
		</LoginFrame>
	</div>
</template>
<script>
  import { Notification } from 'element-ui'
  import LoginFrame from './widget/LoginFrame.vue'

	export default {
    data() {
      return {
        user: this.$store.state.user,
        redirect: this.$store.state.route.query.redirect
      }
    },
		components:{
      LoginFrame
		},
		methods:{
			login() {
			  let that = this
				this.user.httpLogin(function (response) {
          if (that.redirect) {
            that.$router.push(that.redirect)
          }else {
            that.$router.push('/matter/list/root')

          }
        },function (response) {
          that.user.defaultErrorHandler(response)
        })
			},
      reset(){
        this.$router.push('/user/password/reset')
      }
		},
		mounted(){
      this.user.errorMessage = null
		}
	}

</script>
<style lang="less" rel="stylesheet/less">

</style>


