import BaseEntity from '../base/BaseEntity'
import Filter from '../base/Filter'
import { MimeType } from '../base/MimeType'
import { endWith, startWith } from '../filter/str'

export default class Matter extends BaseEntity {
  constructor (args) {
    super(args)
    this.puuid = null
    this.userUuid = null
    this.dir = false
    this.name = null
    this.md5 = null
    this.size = 0
    this.privacy = true
    this.path = null

    //本地操作变量
    this.newFolderStatus = false
    this.showOperation = false
    this.renameStatus = false
  }

  getFilters () {
    return [
      new Filter(Filter.prototype.Type.INPUT, '父级菜单uuid', 'puuid'),
      new Filter(Filter.prototype.Type.INPUT, '用户ID', 'userUuid'),
      new Filter(Filter.prototype.Type.INPUT, '关键字', 'name'),
      new Filter(Filter.prototype.Type.SORT, '是否是文件夹', 'dir'),
      new Filter(Filter.prototype.Type.SORT, '文件夹', 'orderDir'),
      new Filter(Filter.prototype.Type.SORT, '创建时间', 'orderCreateTime'),
      new Filter(Filter.prototype.Type.SORT, '大小', 'orderSize'),
      new Filter(Filter.prototype.Type.SORT, '名称', 'orderName'),
      new Filter(Filter.prototype.Type.INPUT, '后缀名', 'extensions')
    ]
  }

  static URL_MATTER_CREATE_DIRECTORY = '/matter/create/directory'
  static URL_MATTER_DELETE = '/matter/delete'
  static URL_MATTER_DELETE_BATCH = '/matter/delete/batch'
  static URL_MATTER_RENAME = '/matter/rename'
  static URL_MATTER_MOVE = '/matter/move'
  static URL_MATTER_DOWNLOAD = '/matter/download'

  render (obj) {
    super.render(obj)
  }

  getIcon () {
    let extensionWord = this.name.substring(this.name.lastIndexOf('.'))
    if (MimeType[extensionWord] && startWith(MimeType[extensionWord], 'application/pdf')) {
      return require('../../assets/img/file/pdf.png')
    }
    if (MimeType[extensionWord] && (startWith(MimeType[extensionWord], 'application/msword') || startWith(MimeType[extensionWord], 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'))) {
      return require('../../assets/img/file/doc.png')
    }
    if (MimeType[extensionWord] && (startWith(MimeType[extensionWord], 'application/vnd.ms-powerpoint') || startWith(MimeType[extensionWord], 'application/vnd.openxmlformats-officedocument.presentationml.presentation'))) {
      return require('../../assets/img/file/ppt.png')
    }
    if (MimeType[extensionWord] && (startWith(MimeType[extensionWord], 'application/vnd.ms-excel') || startWith(MimeType[extensionWord], 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'))) {
      return require('../../assets/img/file/xls.png')
    }
    if (MimeType[extensionWord] && startWith(MimeType[extensionWord], 'audio')) {
      return require('../../assets/img/file/audio.png')
    }
    if (MimeType[extensionWord] && startWith(MimeType[extensionWord], 'video')) {
      return require('../../assets/img/file/video.png')
    }
    if (MimeType[extensionWord] && startWith(MimeType[extensionWord], 'text')) {
      return require('../../assets/img/file/text.png')
    }
    if (MimeType[extensionWord] && startWith(MimeType[extensionWord], 'image')) {
      return require('../../assets/img/file/image.png')
    }
    if (MimeType[extensionWord] && (endWith(MimeType[extensionWord], 'zip') || endWith(MimeType[extensionWord], 'rar') || endWith(MimeType[extensionWord], '7z') || endWith(MimeType[extensionWord], 'gz'))) {
      return require('../../assets/img/file/archive.png')
    }
    return require('../../assets/img/file/file.png')
  }

  httpMatterCreateDirectory (userUuid, puuid, successCallback, errorCallback) {
    let that = this
    let form = {'userUuid': userUuid, 'name': this.name}
    if (puuid) {
      form.puuid = puuid
    } else {
      form.puuid = 'root'
    }
    this.httpPost(Matter.URL_MATTER_CREATE_DIRECTORY, form, function (response) {
      that.render(response.data.data)
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpMatterDelete (successCallback, errorCallback) {
    this.httpPost(Matter.URL_MATTER_DELETE, {'uuid': this.uuid}, function (response) {
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpMatterDeleteBatch (uuids, successCallback, errorCallback) {
    this.httpPost(Matter.URL_MATTER_DELETE_BATCH, {'uuids': uuids}, function (response) {
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpMatterRename (successCallback, errorCallback) {
    let that = this
    this.httpPost(Matter.URL_MATTER_RENAME, {'uuid': this.uuid, 'name': this.name}, function (response) {
      that.render(response.data.data)
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpMatterMove (srcUuids, destUuid, successCallback, errorCallback) {
    let form = {'srcUuids': srcUuids}
    if (destUuid) {
      form.destUuid = destUuid
    } else {
      form.destUuid = 'root'
    }
    this.httpPost(Matter.URL_MATTER_MOVE, form, function (response) {
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

  httpMatterDownload (successCallback, errorCallback) {
    this.httpPost(Matter.URL_MATTER_DOWNLOAD, {'uuid': this.uuid}, function (response) {
      typeof successCallback === 'function' && successCallback(response)
    }, errorCallback)
  }

}


