<template>
	<li class="nav-item nav-first-level" :class="{'active':menu.active, 'current':current(menu) || shouldActive(menu)}">

		<a href="javascript:void(0)" @click="menuClick(menu)">

			<span class="ml50">
				<i v-if="menu.icon" class="w14" :class="menu.icon"></i>
				<span class="nav-label">{{menu.name}}</span>

				<i v-if="menu.children && menu.children.length" class="pull-right fa"
				   :class="{'fa-angle-left':!menu.active,'fa-angle-down':menu.active}"></i>
			</span>

		</a>

	</li>
</template>
<script>
  import Menu from '../../model/frontend/Menu'
  import { startWith } from '../../model/filter/str'
	export default {
    props: {
      menu: {
        type: Menu,
        required: true
      }
    },
    computed: {},
    methods: {
      current (menu) {
        if (!menu.children || menu.children.length === 0) {
          if (menu.router.path === this.$store.state.route.path) {
            return true
          }

          if (menu.router.path === '/') {
            if (this.$store.state.route.path === '/by' || this.$store.state.route.path === '/by/index') {
              return true
            }

          }

        }

        return false
      },
      menuClick (menu) {

        if (!menu.children || menu.children.length === 0) {
          if (menu.router.path !== this.$store.state.route.fullPath) {
            this.$router.push(menu.router)
          }
        } else {
          menu.active = !menu.active
        }

      },
      shouldActive (menu) {

        return (startWith(this.$route.path, '/matter/list') && startWith(menu.router.path, '/matter/list'))

      }
    },
    watch: {},
    mounted () {

    }
	}

</script>
<style>

</style>


