<template>
	<div id="body" class="fixed-sidebar">

		<div id="wrapper">
			<TopNavigation/>

			<div id="page-wrapper" :class="{'show-drawer':$store.state.config.showDrawer}" @click="blankClick">

				<SideNavigation/>

				<!--<div>

					<BtnDropdown />

					<div class="clearfix"></div>
				</div>-->

				<div>
					<router-view></router-view>
				</div>


			</div>

		</div>

	</div>
</template>
<script>
  import { Notification } from 'element-ui'
  import enquire from 'enquire.js/dist/enquire'
  import SideNavigation from '../view/layout/SideNavigation.vue'
  import TopNavigation from '../view/layout/TopNavigation.vue'
  import AvatarDropdownUl from '../view/layout/widget/AvatarDropdownUl'
  import BtnDropdown from '../view/layout/widget/BtnDropdown.vue'

  export default {
    data () {
      return {
        user: this.$store.state.user,
        config: this.$store.state.config
      }
    },
    components: {
      SideNavigation,
      TopNavigation,
      AvatarDropdownUl,
      BtnDropdown
    },
    methods: {
      blankClick () {
        if (this.config.mobile) {
          if (this.config.showDrawer) {
            this.$store.state.config.showDrawer = false
          }
        }
      },
      listenResponsiveEvent () {
        let that = this
        enquire.register('(max-width: 768px)', {
          match: function () {
            that.$store.state.config.mobile = true
          },
          unmatch: function () {
            that.$store.state.config.mobile = false
          }
        })
      }
    },
    mounted () {
      let that = this

      //验证用户权限,后面放出验证。
      /*if (this.user && this.user.role) {

      } else {
        Notification.error({
          message: '没有权限'
        })
        this.$router.push('/user/login')
      }
*/
      this.listenResponsiveEvent()

    }
  }

</script>
<style lang="less" rel="stylesheet/less">

	@import "../assets/css/global/variables";

	.fixed-sidebar {
		height: 100%;
		#wrapper {
			height: 100%;
		}
	}

	#page-wrapper {
		position: relative;
		margin-top: @top-navigation-height + 10;
		//min-height: 1200px;
		padding-bottom: 40px;

		-webkit-transition: all 0.4s;
		-moz-transition: all 0.4s;
		-o-transition: all 0.4s;
		transition: all 0.4s;

		background-color: #f3f3f4;

		&.show-drawer {
			//大屏幕
			@media (min-width: @screen-sm-min) {
				margin-left: @sidebar-width;
			}

			//小屏幕
			@media (max-width: @screen-xs-max) {
				margin-left: 0;
			}
		}
	}

</style>


