import Menu from './Menu'
import Vue from 'vue'

export default class MenuManager {

}

MenuManager.refreshMenus = function (user) {
  let menus = []

 /* //个人中心
  let menuRecent = new Menu('个人中心', '/user/profile', false, 'fa fa-clock-o')
  menus.push(menuRecent)*/

  //全部
  let menuAll = new Menu('全部', '/matter/list/root', false, 'fa fa-th')
  menus.push(menuAll)

  //文档
  let menuWord = new Menu('文档', '/matter/word/list',false,'fa fa-file-text-o')
  menus.push(menuWord)

  //图片
  let menuPicture = new Menu('图片', '/matter/photo/list', false, 'fa fa-picture-o')
  menus.push(menuPicture)

  //视频
  let menuVideo = new Menu('视频', '/matter/video/list',false,'fa fa-film')
  menus.push(menuVideo)

  //音乐
  let menuMusic = new Menu('音乐', '/matter/music/list',false,'fa fa-music')
  menus.push(menuMusic)

  //用户列表
  if(user.role === 'ADMINISTRATOR'){
    let menuUser = new Menu('用户列表', '/user/list',false,'fa fa-user')
    menus.push(menuUser)
  }


  return menus
}


