import BaseEntity from '../base/BaseEntity'
import { containStr, endWith, getExtension, startWith } from '../filter/str'
import Vue from 'vue'

//当前tank处于怎样的步骤中
let Procedure = {
  'FREE': 'FREE',
  'FETCHING_UPLOAD_TOKEN': 'FETCHING_UPLOAD_TOKEN',
  'UPLOADING': 'UPLOADING',
  'FETCHING_DOWNLOAD_TOKEN': 'FETCHING_DOWNLOAD_TOKEN',
  'DOWNLOADING': 'DOWNLOADING'
}

export default class Tank extends BaseEntity {
  constructor (filter = '*', privacy = true, maxSize = 0, uploadHint = null, userUuid = null ,puuid = null) {
    super()

    this.memberUuid = null
    this.userUuid = null
    this.entityType = null
    this.entityUuid = null
    this.name = null

    this.bucket = null
    this.host = null
    this.url = null
    this.confirmed = false

    this.size = 0
    this.type = null
    this.filter = filter
    this.privacy = privacy
    this.remark = null

    //当前的上传token.
    /*this.tankToken = new TankToken()*/
    this.userUuid = userUuid
    this.puuid = puuid

    //本地字段
    //允许上传的最大大小。
    this.maxSize = maxSize
    //给用户的提示文字
    this.uploadHint = uploadHint
    //浏览器中选择好的原生file，未作任何处理。
    this.file = null
    this.disabled = false
    //当前文件处于哪一个步骤中
    this.procedure = Procedure.FREE
    //当前上传进度的数值 0-1之间
    this.progress = 0
    //实时上传速度 byte/s
    this.speed = 0

  }

  static URL_MATTER_UPLOAD = '/matter/upload'
  static URL_TOKEN_FETCH = '/admin/tank/token/fetch'
  static URL_DOWNLOAD = '/admin/tank/download/'
  static URL_CONFIRM = '/admin/tank/confirm/'

  render (obj) {
    super.render(obj)
  }

  getForm () {

    return {

      memberUuid: this.memberUuid,
      userUuid: this.userUuid,
      entityType: this.entityType,
      entityUuid: this.entityUuid,
      name: this.name,
      size: this.size,
      type: this.type,
      filter: this.filter,
      privacy: this.privacy,
      remark: this.remark

    }

  }

  //从file中装填metaData
  validate () {

    if (!this.file) {
      this.errorMessage = '请选择上传文件'
      return false
    }

    this.name = this.file.name
    if (!this.name) {
      this.errorMessage = '请选择上传文件'
      return false
    }

    this.size = this.file.size
    this.type = this.file.type
    if (!this.type) {
      if (endWith(this.name, 'zip')) {
        this.type = 'application/zip'
      } else if (endWith(this.name, 'rar')) {
        this.type = 'application/rar'
      } else if (endWith(this.name, 'apk')) {
        this.type = 'application/vnd.android.package-archive'
      } else {
        this.type = 'application/octet-stream'
      }
    }
    this.errorMessage = null
    return true

  }

  //具有了这最基本的四种属性，才能叫做“存在”,同时还必须被确认过了。
  exist () {
    //return this.name && this.url && this.size && this.type && this.confirmed
    return this.name && this.size && this.type && this.confirmed
  }

  //公有图片链接。
  publicImgUrl () {
    if (!this.exist()) {
      return null
    }
    if (!this.privacy && this.confirmed) {
      if (startWith(this.type, 'image')) {
        return this.url
      } else {
        return null
      }
    } else {
      return null
    }
  }

  //文件图标。
  fileIcon (inEditor = false) {

    //给不同的文件准备不同的图标
    if (startWith(this.type, 'application/pdf')) {
      if (inEditor) {
        return require('../../assets/img/file/pdf.png')
      } else {
        return 'fa fa-matter-pdf-o color-pdf'
      }
    }
    if (startWith(this.type, 'application/msword') || startWith(this.type, 'application/vnd.openxmlformats-officedocument.wordprocessingml.document')) {
      if (inEditor) {
        return require('../../assets/img/file/doc.png')
      } else {
        return 'fa fa-matter-word-o color-doc'
      }
    }
    if (startWith(this.type, 'application/vnd.ms-powerpoint') || startWith(this.type, 'application/vnd.openxmlformats-officedocument.presentationml.presentation')) {
      if (inEditor) {
        return require('../../assets/img/file/ppt.png')
      } else {
        return 'fa fa-matter-powerpoint-o color-ppt'
      }
    }
    if (startWith(this.type, 'application/vnd.ms-excel') || startWith(this.type, 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet')) {
      if (inEditor) {
        return require('../../assets/img/file/xls.png')
      } else {
        return 'fa fa-matter-excel-o color-xls'
      }
    }
    if (startWith(this.type, 'audio')) {
      if (inEditor) {
        return require('../../assets/img/file/audio.png')
      } else {
        return 'fa fa-matter-audio-o color-audio'
      }
    }
    if (startWith(this.type, 'video')) {
      if (inEditor) {
        return require('../../assets/img/file/video.png')
      } else {
        return 'fa fa-matter-movie-o color-video'
      }
    }
    if (startWith(this.type, 'text')) {
      if (inEditor) {
        return require('../../assets/img/file/text.png')
      } else {
        return 'fa fa-matter-text-o color-text'
      }
    }
    if (startWith(this.type, 'image')) {
      if (inEditor) {
        return require('../../assets/img/file/image.png')
      } else {
        return 'fa fa-matter-image-o color-image'
      }
    }
    if (endWith(this.name, 'zip') || endWith(this.name, 'rar') || endWith(this.name, '7z') || endWith(this.name, 'gz')) {
      if (inEditor) {
        return require('../../assets/img/file/archive.png')
      } else {
        return 'fa fa-matter-archive-o color-archive'
      }
    }

    if (inEditor) {
      return require('../../assets/img/file/file.png')
    } else {
      return 'fa fa-matter-o text-success'
    }

  }

  //验证过滤器有没有误填写，这个方法主要给开发者使用。
  validateFilter () {

    let filter = this.filter
    if (filter === null || filter === '') {
      this.errorMessage = '过滤器设置错误，请检查-1'
      console.error('过滤器设置错误，请检查.-1')
      return false
    }
    if (filter !== '*') {
      let regex1 = /^(image|audio|video|text)(\|(image|audio|video|text))*$/g
      let regex2 = /^(\.[\w]+)(\|\.[\w]+)*$/
      // 测试几种特殊类型 image|audio|video|text

      if (!regex1.test(filter)) {
        //测试后缀名
        if (!regex2.test(filter)) {
          this.errorMessage = '过滤器设置错误，请检查-2'
          console.error('过滤器设置错误，请检查.-2')
          return false
        }
      }
    }

    //validate privacy
    let privacy = this.privacy
    if (privacy !== true) {
      if (privacy !== false) {
        this.errorMessage = 'privacy属性为Boolean类型'
        console.error('privacy属性为Boolean类型.')
        return false
      }
    }

    return true
  }

  //验证用户上传的文件是否符合过滤器
  validateFileType () {
    if (!this.filter) {
      this.errorMessage = '该过滤条件有问题'
      return false
    }
    if (this.filter === '*') {
      this.errorMessage = null
      return true
    }

    let extension = getExtension(this.name)
    let simpleType = this.type.substring(0, this.type.indexOf('/'))

    //专门解决android微信浏览器中名字乱命名的bug.
    if (startWith(this.name, 'image%3A')) {
      extension = 'jpg'
      simpleType = 'image'
    } else if (startWith(this.name, 'video%3A')) {
      extension = 'mp4'
      simpleType = 'video'
    } else if (startWith(this.name, 'audio%3A')) {
      extension = 'mp3'
      simpleType = 'audio'
    }

    if (containStr(this.filter, extension)) {
      this.errorMessage = null
      return true
    }

    if (simpleType) {
      if (containStr(this.filter, simpleType)) {
        this.errorMessage = null
        return true
      }
    }
    this.errorMessage = '您上传的文件格式不符合要求'
    return false
  }

  //开始下载
  download () {

    if (this.privacy) {
      window.open(Vue.http.options.root + Tank.URL_DOWNLOAD + this.uuid)
    } else {
      window.open(this.url)
    }

  }

  //在上传的途中，清空一切，从头开始。
  clear () {

    //filter,privacy不变
    let newTank = new Tank()
    newTank.filter = this.filter
    newTank.privacy = this.privacy
    newTank.errorMessage = this.errorMessage

    this.render(newTank)

  }

  //去oss上确认该文件的存在
  httpConfirm (successMessage, errorMessage) {
    let that = this

    if (!this.uuid) {
      this.errorMessage = '不能正常确认文件'
      return
    }

    this.httpPost(Tank.URL_CONFIRM + that.uuid, {}, successMessage, errorMessage)
  }

  //获取上传的token
  httpTokenFetch (successMessage, errorMessage) {
    let that = this

    if (!this.validate()) {
      return
    }

    this.httpPost(Tank.URL_TOKEN_FETCH, this.getForm(), function (response) {

      that.render(response.data.data);

      (typeof successMessage === 'function') && successMessage()

    }, errorMessage)
  }

  //文件上传
  httpUpload (successCallback, failureCallback) {

    let that = this

    //验证是否装填好
    if (!this.validate()) {
      return
    }

    //验证用户填写的过滤条件是否正确
    if (!this.validateFilter()) {
      return
    }

    //验证是否满足过滤器
    if (!this.validateFileType()) {
      return
    }

    //（兼容性：chrome，ff，IE9及以上）
    let formData = new FormData()

    formData.append('puuid',that.puuid)
    formData.append('userUuid',that.userUuid)
    formData.append('file', that.file)


    that.procedure = Procedure.UPLOADING
    that.httpPost(Tank.URL_MATTER_UPLOAD, formData, function (response) {
      that.confirmed = true
      that.procedure = Procedure.FREE
      typeof successCallback === 'function' && successCallback(response)

    }, function (response) {
      that.procedure = Procedure.FREE
      that.errorMessage = '上传出错，请稍后重试'
      that.clear()

      that.defaultErrorHandler(response, failureCallback)

    }, {
      progress: function (event) {

        //上传进度。
        that.progress = event.loaded / event.total

      }
    })

  }

}
Tank.prototype.Procedure = Procedure


