import Base from "../base/Base";

export default class Menu extends Base{
  constructor(name, path, active = false, icon = null, children = []){
    super();


    this.name = name;
    this.active = active;
    this.icon = icon;
    this.router = {
      path: path
    };
    this.children = children;
  }


  add(menu) {
    this.children.push(menu);
  }
}






