<template>
	<nav ref="menuNav" class="side-navigation" :class="{'show-drawer':showDrawer}">
		<div class="sidebar-collapse">

			<div class="avatar-box">
				<div class="avatar">
					<router-link :to="'/user/profile'">
						<img :src="user.getAvatarUrl()" alt="logo">
					</router-link>
				</div>
				<div class="operation">

					<el-button type="primary" size="small" @click.stop.prevent="logout" plain>退出</el-button>

				</div>
			</div>



			<!--<div class="menu-header">
				<div class="logo-area" @click="goToProfile">
					<img alt="image" class="img-circle w80" :src="user.getAvatarUrl()"/>
				</div>
				<div class="text-area">
					<div class="nickname">
						{{user.nickname}}
					</div>
				</div>
			</div>-->

			<ul class="nav mt20">
				<SideMenu v-for="(menu,index) in user.menus" :key="index" :menu="menu"/>
			</ul>

		</div>
	</nav>
</template>
<script>
  import { MessageBox } from 'element-ui'
  import SideMenu from './SideMenu.vue'
  import Login from '../login/Login.vue'
  import $ from 'jquery'

  export default {
    data () {
      return {
        dialogVisible: false,
        user: this.$store.state.user
      }
    },
    computed: {
      showDrawer () {
        return this.$store.state.config.showDrawer
      },
      mobile () {
        return this.$store.state.config.mobile
      }
    },
    components: {
      SideMenu,
      Login
    },
    watch: {},
    methods: {
      logout () {
        if(this.user.uuid){
          this.user.httpLogout()
        }

        this.$router.push('/user/login')
      },
      goToProfile () {
        this.$router.push('/by/user/profile/' + this.user.uuid)
      }
    },
    mounted () {
      let that = this
    }
  }


</script>
<style lang="less" rel="stylesheet/less">
	@import "../../assets/css/global/variables";

	@nav-bg: #3A3F51;
	@nav-text-color: #222;
	@font-highlight-color: #ddd;
	@left-border-color: @brand-primary;

	//大屏幕
	@media (min-width: @screen-lg-min) {
		.menu-header {
			display: none;
		}
	}

	//中屏幕
	@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
		.menu-header {
			display: none;
		}
	}

	//小屏幕
	@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
		.menu-header {
			display: none;
		}
	}

	//手机屏幕
	@media (max-width: @screen-xs-max) {

		.side-navigation {
			background: @top-navigation-background;
		}
	}

	//左侧菜单block.
	.side-navigation {

		overflow: auto;

		-webkit-transition: all 0.4s;
		-moz-transition: all 0.4s;
		-o-transition: all 0.4s;
		transition: all 0.4s;

		position: fixed;
		width: @sidebar-width;

		left: -@sidebar-width;
		top: @top-navigation-height;
		bottom: 0;
		z-index: 2000;

		background: darken(#e3f3f4, 10%);

		&.show-drawer {
			left: 0;
		}

		.sidebar-collapse {
			padding-bottom: 40px;
			.avatar-box {
				text-align: center;
				.avatar {
					width: 65px;
					margin: 20px auto 0;
					img {
						border: 1px solid transparent;
						border-radius: 100%;
					}
				}
				.operation {
					width: 100px;
					margin: 20px auto 0;
				}
			}
		}

		.app-name-box {
			text-align: center;
			vertical-align: middle;
			display: table-cell;
			width: @sidebar-width;
			background-color: rgba(0, 0, 0, .05);
			height: @top-navigation-height;
			a {
				font-size: 16px;
				color: white;
			}
		}

		//放头像和用户名的地方。
		.menu-header {

			border-bottom: 1px solid #2F4458;
			.logo-area {
				cursor: pointer;
				text-align: center;
				padding: 20px 0;
			}

			.text-area {
				.nickname {
					text-align: center;
					color: @nav-text-color;
					font-weight: bold;
				}
				.role {
					text-align: center;
					color: @nav-text-color;
				}
			}

		}

		//分割线
		.separate-line {
			margin-top: 20px;
			padding-bottom: 5px;
			border-bottom: 1px solid #666666;

			span {
				margin-left: 20px;
				color: #777;
			}
		}

		.nav {
			li {

				&.active {

					//只有紧接的这一层a才会高亮。
					> a {
						color: @font-highlight-color;
					}
				}

				a {

					color: @nav-text-color;

					padding: 12px 20px;
					&:hover, &:focus {
						//color: @neobay-light-blue;
						background-color: @top-navigation-background;

					}

					i {
						margin-right: 6px;
					}
				}
			}

			.nav-first-level {
				&.active {
				}
				&.current {
					&:before{
						position: absolute;
						content: "";
						z-index: 2000;
						left: 0;
						width: 4px;
						height: 28px;
						top: 50%;
						margin: -14px 0 0;
						background: #3b8cff;
					}
					&:after{
						position: absolute;
						content: "";
						z-index: 2000;
						right: 0;
						width: 4px;
						height: 28px;
						top: 50%;
						margin: -14px 0 0;
						background: #3b8cff;
					}


					a {
						color: #3b8cff;
					}


					/*background-color: black;*/
				}
			}
			&.nav-second-level {

				> li {

					> a {
						padding-left: 43px;
					}
					&.active {
						border: none;
					}
					&.current {
						background-color: black;
					}
				}
			}
			&.nav-third-level {

				> li {
					> a {
						padding-left: 57px;
					}
					&.current {
						background-color: black;
					}
				}
			}
		}
	}

</style>


