import Vue from 'vue'
import Vuex from 'vuex'
import User from '../model/user/User'

Vue.use(Vuex)

let currentUser = new User()
currentUser.refreshMenus()

const state = {
  config: {
    mobile: false,
    showDrawer: true
  },

  debug: true,

  user: currentUser,

  host: '/api',

  breadcrumbs: null,

  //当前版本信息。
  version: {
    identifier: 'tank-front',
    platform: 'WEB',
    versionCode: '1',
    versionName: '1.0'
  },

  //存放移动的文件uuid
  moveUuids:[]
}

//从本地存储中读取用户信息。
state.user.renderFromLocalStorage()

const getters = {
  getConfig (state) {
    return state.config
  }
}

const mutations = {}

const actions = {}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



