let ExtensionsType = {
  WORD: 'txt,doc,docx,ppt,pptx,xls,xlsx,pdf,TXT,DOC,DOCX,PPT,PPTX,XLS,XLSX,PDF',
  PHOTO: 'jpg,jpeg,png,gif,JPG,JPEG,PNG,GIF',
  MUSIC: 'mp3,ogg,aac,MP3,OGG,AAC',
  VIDEO: 'mp4,avi,mkv,rmvb,wmv,MP4,AVI,MKV,RMVB,WMV'
}

let ExtensionsTypeMap = {
  WORD: {
    name: '文本文件',
    value: 'txt,doc,docx,ppt,pptx,xls,xlsx,pdf,TXT,DOC,DOCX,PPT,PPTX,XLS,XLSX,PDF'
  },
  PHOTO: {
    name:'图片文件',
    value: 'jpg,jpeg,png,gif,JPG,JPEG,PNG,GIF'
  },
  MUSIC: {
    name: '音频文件',
    value: 'mp3,ogg,aac,MP3,OGG,AAC'
  },
  VIDEO: {
    name: '视频文件',
    value: 'mp4,avi,mkv,rmvb,wmv,MP4,AVI,MKV,RMVB,WMV'
  }
}

let ExtensionsTypeList = []
for( let key in ExtensionsTypeMap){
  if (ExtensionsTypeMap.hasOwnProperty(key)) {
    ExtensionsTypeList.push(ExtensionsTypeMap[key])
  }
}

export { ExtensionsType, ExtensionsTypeMap, ExtensionsTypeList }


