<template>
	<div class="user-list">
		<div>
			<div class="row">
				<div class="col-md-12">
					<div class="pedia-navigation">
						<span class="item active">
							用户列表
						</span>
					</div>
				</div>

				<div class="col-md-12">
					<NbFilter :pager="pager" :callback="search">
						<router-link class="btn btn-sm btn-primary" to="/user/create">
							<i class="fa fa-plus"></i>
							添加用户
						</router-link>
					</NbFilter>
				</div>

				<div>
					<div class="col-md-12" v-for="(item,index) in pager.data">
						<div class="bg-white p10 mb10 br4">
							<div class="media">

								<div class="pull-left">
									<router-link :to="'/user/profile/' + item.uuid">
										<img class="img-circle img-md" :src="item.getAvatarUrl()">
									</router-link>
								</div>

								<div class="media-body">

									<div>
										<span class="f16">
											<router-link class="black" :to="'/user/profile/'+item.uuid">
													{{item.username}}
											</router-link>
										</span>
									</div>
									<div class="row">
										<div class="mt5 col-md-4 col-sm-6 col-xs-12">
											<div>
												邮箱：{{item.email}}
											</div>
										</div>
										<div class="mt5 col-md-4 col-sm-6 col-xs-12">
											<div>
												手机：{{item.phone}}
											</div>
										</div>
										<div class="mt5 col-md-4 col-sm-6 col-xs-12">
											<div>
												上次使用时间：{{item.lastTime | simpleDateTime}}
											</div>
										</div>
										<div class="mt5 col-md-4 col-sm-6 col-xs-12">
											<div>
												状态：{{item.getStatusName()}}
											</div>
										</div>
									</div>
									<div>
											<span class="pull-right action-buttons">
												<router-link :to="'/matter/list/root?userUuid=' + item.uuid" title="查看他的文件">
													<i class="fa fa-th text-primary f18"></i>
												</router-link>
												<router-link :to="'/user/edit/'+item.uuid" title="编辑">
													<i class="fa fa-pencil text-primary f18"></i>
												</router-link>
												<a href="javascript:void(0)" title="修改状态" @click.stop.prevent="changeStatus(item)">
						               <i class="fa fa-power-off f18" :class="'text-' + item.getStatusStyle()"></i>
						            </a>
											</span>
									</div>

								</div>

							</div>
						</div>
					</div>
				</div>

				<div class="col-md-12 mt20">
					<NbPager :pager="pager" :callback="refresh"></NbPager>
				</div>

			</div>




		</div>
	</div>
</template>
<script>
  import NbFilter from '../../components/filter/NbFilter.vue'
  import NbPager from '../../components/NbPager.vue'
  import Pager from '../../model/base/Pager'
  import User from '../../model/user/User'
  import { MessageBox, Notification } from 'element-ui'

	export default {
    data(){
      return{
        pager: new Pager(User)
      }
    },
		components:{
      NbFilter,
      NbPager
		},
		methods:{
      changeStatus (user, successCallback, errorCallback) {
        let that = this
        MessageBox.confirm('此操作将修改该用户的状态, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function () {
            user.httpUserChangeStatus(function (response) {
              user.httpDetail(function () {
                let message = '激活成功！'
                if (user.status !== 'OK') {
                  message = '注销成功！'
                }
                Notification.success({
                  message: message
                })
              })
            })

          },
          function () {
            if (typeof errorCallback === 'function') {
              errorCallback()
            }
          }
        )
      },
      search () {
        this.pager.page = 0
        this.refresh()
      },
      refresh () {
        this.pager.httpFastPage()
      }

		},
		mounted(){
      this.refresh()
		}
	}


</script>
<style lang="less" rel="stylesheet/less">
	.user-list{

	}
</style>


