import Vue from 'vue'
import Router from 'vue-router'
import store from '../vuex/index'
import FrameView from '../view/Frame.vue'
import UserLogin from '../view/login/Login.vue'
import UserProfile from '../view/user/Profile.vue'
import UserChangePassword from '../view/user/ChangePassword.vue'
import UserList from '../view/user/List.vue'
import UserCreate from '../view/user/Create.vue'
import MatterList from '../view/matter/List.vue'
import MatterWordList from '../view/matter/word/List.vue'
import MatterPhotoList from '../view/matter/photo/List.vue'
import MatterVideoList from '../view/matter/video/List.vue'
import MatterMusicList from '../view/matter/music/List.vue'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  linkActiveClass: 'is-link-active',
  scrollBehavior: () => ({y: 0}),
  routes: [
    {
      path: '/',
      component: FrameView,
      redirect: 'matter/list/root',
      children: [
        {
          path: 'user/profile',
          name: 'userProfile',
          component: UserProfile,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'user/profile/:uuid',
          name: 'currentUserProfile',
          component: UserProfile,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'user/list',
          name: 'userList',
          component: UserList,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'user/create',
          name: 'userCreate',
          component: UserCreate,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'user/edit/:uuid',
          name: 'userEdit',
          component: UserCreate,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'user/change/password',
          name: 'userChangePassword',
          component: UserChangePassword,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'matter/list/:puuid',
          name: 'matterList',
          component: MatterList,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'matter/word/list',
          name: 'matterWordList',
          component: MatterWordList,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'matter/photo/list',
          name: 'matterPhotoList',
          component: MatterPhotoList
        },
        {
          path: 'matter/video/list',
          name: 'matterVideoList',
          component: MatterVideoList
        },
        {
          path: 'matter/music/list',
          name: 'matterMusicList',
          component: MatterMusicList,
          meta: {
            requiresAuth: true
          }
        }
      ]
    },
    {
      path: '/user/login',
      name: 'userLogin',
      component: UserLogin
    }

  ]
})


//add global interceptor.
router.beforeEach((to, from, next) => {

  //handle auth feature.
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    if (store.state.user.isLogin) {
      next()
    } else {
      next({
        path: '/user/login',
        query: {redirect: to.fullPath}
      })
    }
  } else {
    next()
  }



})

export default router



